.app-wrapper{
    display:grid;
    margin: 0 auto;
    width: auto;
    grid-template-areas:
    "header header"
    "nav content";
    grid-template-rows: 60px 2fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
    background-color: gainsboro;
}

.app-wrapper-content{
    grid-area: content;
    background-color: silver;
    width: 170vh;
    border-radius: 10px;
    margin-right: 10px;
}
